.c-hamburger {
  position: fixed;
  top: 1.8125rem;
  right: 1.5rem;
  width: 1.25rem;
  height: 0.8125rem;
  z-index: 9999;
  cursor: pointer;
  transition: all 0.3s;
  display: block;
}
@media screen and (min-width: 768px) {
  .c-hamburger {
    transform: scale(1.5);
    transform-origin: top right;
  }
}
@media screen and (min-width: 1024px) {
  .c-hamburger {
    display: none;
  }
}

.c-hamburger span {
  position: absolute;
  right: 0;
  display: inline-block;
  height: 0.0625rem;
  background-color: #fff;
  transition: all 0.3s;
}

.c-hamburger span:nth-child(1) {
  width: 100%;
  top: 0;
}

.c-hamburger span:nth-child(2) {
  width: 1rem;
  top: 0.375rem;
  transition: opacity 0.3s;
}

.c-hamburger span:nth-child(3) {
  width: 1rem;
  bottom: 0;
}

.c-hamburger.is-active {
  top: 2.5rem;
  right: 2.5rem;
  width: 1.75rem;
}

.c-hamburger.is-active span:nth-child(1) {
  width: 100%;
  top: 0.375rem;
  transform: rotate(45deg);
}

.c-hamburger.is-active span:nth-child(2) {
  opacity: 0;
}

.c-hamburger.is-active span:nth-child(3) {
  width: 100%;
  bottom: 0.375rem;
  transform: rotate(-45deg);
}

